"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import React, { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Menu, X } from "lucide-react";

export default function Navbar() {
  const [isMobile, setIsMobile] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const pathname = usePathname();

  useEffect(() => {
    const checkIfMobile = () => {
      setIsMobile(window.innerHeight < 768);
    };
    const handleScroll = () => {
      setScrolled(window.innerHeight < 768);
    };

    checkIfMobile();
    window.addEventListener("resize", checkIfMobile);
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.addEventListener("resize", checkIfMobile);
      window.addEventListener("scroll", handleScroll);
    };
  }, []);
  useEffect(() => {
    setIsMenuOpen(false);
  }, [pathname]);

  const navItems = [
    { name: "PORTFOLIO", path: "/" },
    { name: "Home", path: "/" },
    { name: "Testimonies", path: "/testimonies" },
    { name: "Experiences", label: "/experiences" },
    { name: "Contact", label: "/contact" },
  ];

  const navbarVariants = {
    hidden: { opacity: 0, y: -20 },
    visible: { opacity: 1, y: 0 },
  };

  const mobileMenuVariants = {
    closed: { opacity: 0, scale: 0.95, y: -10 },
    open: { opacity: 1, scale: 0 },
  };
  return (
    <motion.nav
      initial="hidden"
      animate="visible"
      variants={navbarVariants}
      transition={{ duration: 0.5 }}
      className={`sticky top-0 z-40 transition-all duration-300 ${
        scrolled ? "py-2 shadow-md" : "py-3"
      }}`}
    >
      <div
        className={`bg-[#98fb98] p-2 mb-2 text-sm rounded-md transition-all duration-300 ${
          scrolled ? "opacity-90" : "opacity-100"
        }`}
      >
        Navbar
      </div>

      <div
        className={`bg-[#98fb98] rounded-lg shadow-md transition-all duration-300 ${
          scrolled ? "bg-opacity-95 backdrop-blur-sm" : "bg-opacity-100"
        }`}
      >
        {isMobile && (
          <div className="p-4 flex justify-between items-center">
            <Link href="/" className="font-bold text-lg">
              PORTFOLIO
            </Link>
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="p-2 rounded-md hover:bg-green-200
        transition-colors duration-200"
              aria-label={isMenuOpen ? "Close Menu" : "Open-Menu"}
            >
              {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        )}
        <AnimatePresence>
          {(isMobile && isMenuOpen) || !isMobile ? (
            <motion.div
              className={`${
                isMobile ? "flex flex-col" : "flex flex-row justify-between"
              } p-4`}
              initial={isMobile ? "closed" : undefined}
              animate={isMobile ? "open" : undefined}
              exit={isMobile ? "closed" : undefined}
              variants={mobileMenuVariants}
            >
              {navItems.map((item) => (
                <Link
                  key={item.name}
                  href={item.path + item.name}
                  className={`px-4 py-2 rounded-md transition-all duration-300 ${
                    pathname === item.path
                      ? "font-bold bg-green-200 "
                      : "hover:bg-green-200"
                  } ${isMobile ? "mb-2 text-center" : ""}}`}
                >
                  {item.name}
                </Link>
              ))}
            </motion.div>
          ) : null}
        </AnimatePresence>
      </div>
    </motion.nav>
  );
}
